/*!
Functions and Mixins
*/

@function px2rem($font-size, $base-font-size: 16) {
    @return $font-size / $base-font-size + rem;
  }
  @mixin px2rem($font-size, $line: 1.5, $base-font-size: 16) {
      font-size: px2rem($font-size, $base-font-size);
      line-height: $line;
  }
  
  @mixin border-radius($radius) {
      border-radius: $radius;
      background-clip: padding-box;
  }
  @mixin border-top-radius($radius) {
      border-top-right-radius: $radius;
      border-top-left-radius: $radius;
      background-clip: padding-box;
  }
  
  @mixin border-right-radius($radius) {
      border-bottom-right-radius: $radius;
      border-top-right-radius: $radius;
      background-clip: padding-box;
  }
  
  @mixin border-bottom-radius($radius) {
      border-bottom-right-radius: $radius;
      border-bottom-left-radius: $radius;
      background-clip: padding-box;
  }
  
  @mixin border-left-radius($radius) {
      border-bottom-left-radius: $radius;
      border-top-left-radius: $radius;
      background-clip: padding-box;
  }
  
  @mixin hideout() {
      position: absolute;
      left: -10000px;
      top: auto;
      width: 1px;
      height: 1px;
      overflow: hidden;
  }
  
  @mixin svg-content($svg, $subset: 'solid') {
      content: url("../../assets/svg/#{$subset}/#{$svg}.svg");
      display: inline-block;
      width: 1em;
      margin-right: 5px;
      position: relative;
      top: 2px;
  }
  